import React from "react";
import styled from "styled-components";
import { ErrorMessage } from "../helpers/form-sections";

export const LabeledInput = ({
  placeholder,
  label,
  id,
  type,
  name,
  required,
  value,
  icon = null,
  isInvalid,
  error,
  ...props
}) => {
  return (
    <InputWrapper>
      <StyledLabel htmlFor={id}>
        {label} {!required && <span> - Optional</span>}
      </StyledLabel>
      <IconInput>
        <StyledInput
          type={type}
          id={id}
          placeholder={placeholder}
          name={name}
          value={value}
          $isInvalid={isInvalid}
          {...props}
        />
        {icon && <IconWrapper>{icon}</IconWrapper>}
      </IconInput>
      <ErrorMessage>
        {isInvalid && <span>{label} is required</span>}
      </ErrorMessage>
    </InputWrapper>
  );
};

const InputWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
`;

const StyledInput = styled.input`
  background: #ffffff;
  border: 2px solid ${({ $isInvalid }) => ($isInvalid ? "#ed5252" : "#eeedf3")};
  border-radius: 8px;
  padding: 16px;
  height: 56px;
  box-sizing: border-box;
  width: 100%;
  font-size: 14px;

  &:active,
  &:focus {
    color: #060831;
    outline: none;
    border: 2px solid #4200ff;
  }

  &:disabled {
    background: #0000001a;
    opacity: 0.5;
  }

  &::placeholder {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #757ba9;
    opacity: 1; // To make it look good on Firefox
  }
`;

const StyledLabel = styled.label`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.2px;
  color: #47474f;

  & span {
    color: #cacfe0;
  }
`;

const IconWrapper = styled.div`
  position: absolute;
  top: 16px;
  right: 19px;
`;

const IconInput = styled.div`
  position: relative;
`;
